export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiDocsModule, NuiSearchModule, NuiSelectModule } from \"@nova-ui/bits\";\n\nimport { HighlightPipeDocsExampleComponent, HighlightPipeExampleComponent } from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: HighlightPipeDocsExampleComponent,\n    },\n    {\n        path: \"highlight-pipe\",\n        component: HighlightPipeDocsExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiDocsModule,\n        NuiSearchModule,\n        NuiSelectModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        HighlightPipeExampleComponent,\n        HighlightPipeDocsExampleComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class PipesModule {\n}\n";